export function LabeledIcon({icon, label}: {
    icon: JSX.Element,
    label: string
}) {
    return (
        <div className='experience-labeled-icon big'>
            {icon}
            {label}
        </div>
    );
}

export function LabeledIconSmall({icon, label}: {
    icon: JSX.Element,
    label: string
}) {
    return (
        <div className='experience-labeled-icon small'>
            {icon}
            {label}
        </div>
    );
}

export function ExperienceItem({itemTitle, children, className, hidden}: 
    {itemTitle: string, children: React.ReactNode, className?: string, hidden?: boolean}) {

    return (
        <div className={`experience-item ${className ?? itemTitle.toLowerCase().replace(' ', '-')} ${hidden ? 'hidden' : ''}`}>
            <div className="experience-item-title">{itemTitle}</div>
            <div className={`experience-item-content ${className ?? itemTitle.toLowerCase().replace(' ', '-')}`}>{children}</div>
        </div>
    );
}

export function High({children} : {children: React.ReactNode}) {
    return (
        <span className="experience-item-content-highlighted">{children}</span>
    )
}

export function HighSpecial({children} : {children: React.ReactNode}) {
    return (
        <span className="experience-item-content-highlighted special">{children}</span>
    )
}
